import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	components: {
		uniNavBar,
		uniPopup
	},
	data() {
		return {
			detailData: {},
			isFxid: false,
			userFeedbackHidden: true, // 默认隐藏
			feedbackContent: '' // 用户反馈内容
		};
	},
	//接受上个页面传过来的值
	onLoad(options) {
		this.detailData = JSON.parse(options.data);

		this.loadNewsList();
		var windowHeight = uni.getSystemInfoSync().windowHeight;
		console.log('windowHeight');
		console.log(windowHeight);
		let hel = 0;
		const query = uni.createSelectorQuery().in(this);
		query
			.select('#bigboxs')
			.boundingClientRect(data => {
				hel = data.height;
				console.log(hel);
				console.log(windowHeight);
				if (hel < windowHeight) {
					console.log(666);
					this.isFxid = true;
				}
			})
			.exec();
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		statusChange(index) {
			switch (index) {
				case 1:
					return '通知';
					break;
				case 2:
					return '公告';
					break;

				case 3:
					return '工作';
					break;
				default:
					return '通知';
			}
		},
		//初始化消息
		loadNewsList() {
			console.log('获取内容');
			console.log(this.detailData);
		},
		//通知公告确认
		confirmMsg() {
			console.log('确认弹出框');
			uni.showLoading({
				title: 'loading'
			});
			setTimeout(() => {
				uni.hideLoading();
				uni.navigateTo({
					url: `/pages/index/index`
				});
			}, 3000);
		},
		//反馈
		fanKui() {
			console.log('反馈');
			this.$nextTick(() => {
				this.$refs.showtip.open();
			});
		},

		cancel(type) {
			this.$refs.showtip.close();
		},
		submitMgs() {
			uni.showLoading({
				title: 'loading'
			});

			setTimeout(() => {
				uni.hideLoading();
				this.detailData.feedback_content = this.feedbackContent;
				var dateee = new Date().toJSON();
				var dates = new Date(+new Date(dateee) + 8 * 3600 * 1000)
					.toISOString()
					.replace(/T/g, ' ')
					.replace(/\.[\d]{3}Z/, '');
				this.detailData.feedback_time = dates;
				this.$refs.showtip.close();
				uni.navigateTo({
					url: `/pages/index/index`
				});
			}, 1500);
		}
	}
};
